<template>
  <div>
    <NavTemplent title="部门指标数据"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getSystemLogList"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="部门">
            <el-select v-model="form.departmentId" placeholder="请选择部门" @change="getSystemLogList">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="姓名">
            <el-select v-model="form.userName" placeholder="请选择姓名">
              <el-option v-for="item in userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div>部门指标列表</div>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="editMessage"><i class="el-icon-plus"></i>添加</el-button>
        </span>
      </div>
      <el-table
        :data="indicatorsList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="用户名">
          <template slot-scope="scope">
            <span>{{scope.row.user_name || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="备注">
          <template slot-scope="scope">
            <span>{{scope.row.remark || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="IP地址">
          <template slot-scope="scope">
            <span>{{scope.row.ip || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间">
          <template slot-scope="scope">
            {{scope.row.add_time * 1000 | dateFormat('YYYY/MM/DD')}}
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editMessage(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="delMessage(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="姓名">
            <el-select v-model="form.name" placeholder="请选择姓名">
              <el-option v-for="item in userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门">
            <el-select v-model="form.department_id" placeholder="请选择部门" @change="getSystemLogList">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="指标">
            <el-select v-model="form.indicators" placeholder="请选择指标">
              <el-option v-for="item in indicatorsList" :key="item.quota_id" :label="item.quota_name" :value="item.quota_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标值">
            <el-input v-model="form.targetValue" placeholder="请输入目标值"></el-input>
          </el-form-item>
          <el-form-item label="单位">
            <el-select v-model="form.unit" placeholder="请选择单位">
              <el-option label="百分比" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标期类型">
            <el-select v-model="form.targetType" placeholder="请选择部门">
              <el-option :value="1" label="日"></el-option>
              <el-option :value="2" label="周"></el-option>
              <el-option :value="3" label="月"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="实际完成值">
            <el-input v-model="form.finishValue" placeholder="请输入完成值"></el-input>
          </el-form-item>
          <el-form-item label="该项得分">
            <el-input v-model="form.score" placeholder="请输入该项得分"></el-input>
          </el-form-item>
          <el-form-item label="选择日期">
            <el-date-picker
              class="dateditor"
              v-model="form.date"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="form.note" placeholder="请输入备注"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import moment from 'moment'
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                total: 0,
                departmentList: [],
                userList: [],
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                systemLogList: [],
                indicatorsList: [],
                editUserMessage: null,
                editUse: null,
                title: '修改用户指标',
                dialogVisible: false,
                menu: false,
                form: {
                    targetType: '',
                    score: '',
                    unit: '',
                    targetValue: '',
                    indicators: '',
                    finishValue: '',
                    date: '',
                    userName: '',
                    departmentId: '',
                    mobile: '',
                    department_id: '',
                    email: '',
                    name: '',
                    note: ''
                }
            }
        },
        methods: {
            getindicatorsList() {
                this.$post('/getQuotaList', {
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum
                }).then(res => {
                    this.indicatorsList = res.list;
                    this.total = res.count
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            getUserList() {
                this.$post('/getUserList').then(res => {
                    this.userList = res.list
                })
            },
            getSystemLogList() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getUserQuotaList', {
                    page: this.page.pageSize,
                    pageNum: this.page.pageNum,
                    start_time: this.startTime,
                    end_time: this.lastTime,
                    user_id: this.form.userName,
                    department_id: this.form.departmentId
                }).then(res => {
                    this.total = res.count;
                    this.systemLogList = res.list
                })
            },
            editMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.menu = false;
                }
            },
            delMessage(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delUserQuota', {
                        user_quota_id: data.user_quota_id
                    }).then(() =>{
                        this.$message.success('删除成功')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
              },
              cancle() {
                  this.dialogVisible = false;
                  this.$emit('close')
              },
            save() {
                console.log(this.form);
                this.$post('addUserQuota', {
                    user_id: this.form.name,
                    target_value: this.form.targetValue,
                    department_id: this.form.department_id,
                    target_date_type: this.form.targetType,
                    complete_value: this.form.finishValue,
                    remark: this.form.note,
                    score: this.form.score,
                    complete_date: moment(this.form.date).format('YYYY/MM/DD'),
                    unit: this.form.unit,
                    quota_id: this.form.indicators
                }).then(() => {
                    this.$message.success('用户新增成功');
                    this.dialogVisible = false;
                    this.$emit('close');
                    this.getSystemLogList()
                })
            }
        },
        mounted() {
            this.getindicatorsList();
            this.getUserList();
            this.getDepartmentList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .dateditor{
    width: 100%;
  }
</style>



